
import { Vue } from 'vue-class-component'
import { Empty } from 'google-protobuf/google/protobuf/empty_pb'
import { Room, Rooms, CreateRoomRequest } from '../nimar_pb'
import { api, metadata } from '../api'
import { ClientReadableStream, Metadata, RpcError } from 'grpc-web'

export default class RoomList extends Vue {
  private room_stream: ClientReadableStream<Rooms>
  rooms: Array<Room> = new Array<Room>()
  is_show_add_room_dialog = false
  add_room_name = ""

  created(): void {
    this.room_stream = api.listRoomStream(new Empty(), metadata)
    this.room_stream.on("data", (rooms: Rooms) => {
      rooms ? this.rooms = rooms.getRoomsList() : this.rooms = new Array<Room>()
    })
    this.room_stream.on("error", (err: RpcError) => {
      console.log(err)
    })
    this.room_stream.on("end", () => {
      console.log("end")
    })
    this.room_stream.on("metadata", (metadata: Metadata) => {
      console.log(metadata)
    })
  }

  show_add_room_dialog() {
    this.is_show_add_room_dialog = true
  }

  add_room() {
    let room_name: string = this.add_room_name
    this.add_room_name = ""
    this.is_show_add_room_dialog = false

    let create_room_request: CreateRoomRequest = new CreateRoomRequest();
    create_room_request.setRoomname(room_name)

    api.createRoom(create_room_request, metadata)
  }
  join_room(roomID: string) {
    this.$router.push('/room?tableid=' + roomID)
  }
}
