
import { GameTable, JoinRoomRequest, Message, Operators, PlayerID, TableStatus } from '@/nimar_pb';
import { Empty } from 'google-protobuf/google/protobuf/empty_pb';
import { ClientReadableStream, RpcError } from 'grpc-web';
import { Vue } from 'vue-class-component';
import { api, metadata } from '../api'

export default class Room extends Vue {
    is_show_player_name_dialog = false
    player_name = ""

    private table_stream: ClientReadableStream<GameTable>
    private message_stream: ClientReadableStream<Message>
    private operators_stream: ClientReadableStream<Operators>

    created() {
        this.is_show_player_name_dialog = true
    }

    join_room() {
        api.getPlayerID(new Empty(), metadata)
            .then((playerid: PlayerID) => {
                let join_request: JoinRoomRequest = new JoinRoomRequest()
                join_request.setPlayername(this.player_name)
                join_request.setPlayerid(playerid.getPlayerid())
                join_request.setRoomid(String(this.$route.query.tableid))

                this.table_stream = api.gameTableStream(join_request, metadata)
                this.table_stream.on("data", (gameTable: GameTable) => {
                    console.log(gameTable)
                })
                this.table_stream.on("error", (err: RpcError) => {
                    console.log(err)
                })

                this.message_stream = api.messageStream(join_request, metadata)
                this.message_stream.on("data", (message: Message) => {
                    console.log(message)
                })
                this.message_stream.on("error", (err: RpcError) => {
                    console.log(err)
                })

                this.operators_stream = api.operatorsStream(join_request, metadata)
                this.operators_stream.on("data", (operators: Operators) => {
                    console.log(operators)
                })
                this.operators_stream.on("error", (err: RpcError) => {
                    console.log(err)
                })

                this.is_show_player_name_dialog = false
            })
    }
}
